<!-- MyPopup.vue -->
<template>
	<view v-if="visible" class="popup">
		<view class="popup-content">
			<text class="popup-title">{{ title }}</text>
			<text class="popup-content-text">{{ content }}</text>
			<view class="popup-buttons">
				<button @click="handleCancel">取消</button>
				<button @click="handleConfirm">确定</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			visible: Boolean,
			title: String,
			content: String
		},
		methods: {
			handleCancel() {
				this.$emit('cancel');
			},
			handleConfirm() {
				this.$emit('confirm');
			}
		}
	};
</script>

